<template>
  <div class="col-12 no_x" v-if="ready">
    <div style='display:flex;flex-direction:row'>
        <div class="col-3">
                  Welcome
                  <br/> 
                  <b>{{ name }}</b><br/>
                  <u>{{ pos }}</u>
                  <br/>
                  Please Sign Here <i class="fa fa-arrow-right"></i>        
        </div> 
        <!---->
        <div class="col-6">
          <VueSignaturePad
              id="signature"
              width="100%"
              height="200px"
              ref="signaturePad"
            />
            <div style='margin-top:0.5em;margin-left:30%;margin-right:30%'>
              <button
                class="btn btn-outline-secondary float-right"
                @click="undo"
              >
                Undo
              </button>
              <button
                class="btn btn-outline-primary float-left"
                @click="save"
                :disabled="processing"
              >
                Sign
              </button>
            </div>
        </div>   
        </div> <br/> 
        <div class="col-3"></div>
        <!---->    
        <!--     
        <div class="col-12" style="overflow:auto">
            <div class="col-12" style='text-align:center'><iframe :src="url" style="background: #FFFFFF;width:80vw!important;height:30em;"></iframe>
                <br/><br/>
            </div>              
        </div>-->
        <div class='col-12 framewrapper'>   
            <div class="col-12 scroll_frame" style='height:100%!important;width:100%!important;padding-left:0;padding-right:0'>                  
                <iframe :src="url" class='col-12 ios_frame' style='height:100%!important;width:100%!important;padding-left:0;padding-right:0'/><br/>
          </div>
        </div>
    
  </div>
</template>
<script>
function getURLParameter(name) {
  return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.search) || [null, ''])[1].replace(/\+/g, '%20')) || null;
}
export default {
    data(){
        return{
            processing:false,
            username:'',
            pos:'TAC Member',
            new_user:false,
            //type_list:['IMC Manager','PTA Repersentative'],
            name:'',
            userlist:[],
            type:'IMC Manager',
            proc_id:'',
            pwd:'',
            pwd_check:'',
            return:'',
            url:'',
            name_select:'',
            ready:false,
            params: window.location.href.substr(window.location.href.indexOf('?')),
        }
    },
async created(){
    let param = new URLSearchParams(this.params);
    if(param.has('id')){
        this.proc_id = param.get('id');
        this.username = param.get('rank');
        if(this.username=='imcmanag'){
          this.pos = 'TAC Chairperson';
        } else {
          this.pos = 'TAC Member';
        }
        this.name = param.get('name');
        console.log(this.proc_id);      
    }      
    var baseurl = "src/Procurement/procurement/backend/ret_stat_cat4.php?p_id=";
    var urlname = this.name;
    urlname = urlname.split(' ').join('%20');
    this.url = baseurl + this.proc_id + "&name=" + urlname;
    console.log(this.url);
    //onload data
    var formData = new FormData();
    formData.append('type',this.type);
    await axios.post('Procurement/procurement/backend/extra_userlist.php',formData)
        .then(
            (response)=>{
                this.userlist = response.data;
            }
        )


    this.ready = true;
},
    
  name: 'user_setup',
  methods: {
      new_list(){
          var formData = new FormData();
            formData.append('type',this.type);
            axios.post('Procurement/procurement/backend/extra_userlist.php',formData)
                .then(
            (response)=>{
                this.userlist = response.data;
                this.name_select='';
            }
        )
      },
    undo() {
      this.$refs.signaturePad.undoSignature();
      this.pwd='';
      this.pwd_check='';
    },
    async save() {
      const { isEmpty, data } = this.$refs.signaturePad.saveSignature();
      if(isEmpty){
          this.$alert('Signature board cannot be empty!');
        } else {
          this.processing = true;
          var sign = data;
          var formData = new FormData();
          formData.append('sign',data);
          formData.append('name',this.name);
          formData.append('pos',this.pos);
          formData.append('proc_id',this.proc_id);
          await axios.post('Procurement/procurement/backend/sign_tender.php',formData)
            .then(
              (response)=>{
                this.$alert('Successfully Signed, return to previous form ...').then(
                  ()=>{
                    this.processing = false;                   
                  }
                )
                window.location.href=this.url;             
              }
            )         
        }     
    }
  }
};
</script>

<style>
#signature {
  border: double 3px transparent;
  border-radius: 5px;
  background-image: linear-gradient(white, white),
    radial-gradient(circle at top left, #4bc5e8, #9f6274);
  background-origin: border-box;
  background-clip: content-box, border-box;
}
</style>



